@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

a, input {
  margin: 0;
  padding: 0;
}

.search-bar {
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: center;
  width: 100%;
  margin-top: 20px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar--large {
  height: 70px;
}

.search-bar--small {
  height: 42px;
}

.search-bar__input {
  border: none;
  outline: none;
  flex-grow: 1;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search-bar__input::placeholder {
  color: #3d4e61;
}

.input--large {
  margin-left: 17px;
  font-size: 16px;
}

.input--small {
  margin-left: 9px;
  font-size: 14px;
}

.search-bar__icon {
  background-image: url("Search.ccd3c07b.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
}

.icon--large {
  width: 19px;
  height: 19px;
  margin-left: 26px;
}

.icon--small {
  width: 11px;
  height: 11px;
  margin-left: 13px;
}

.search-bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar-container:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar__input:focus {
  text-shadow: 0 4px 4px #00000040;
  font-weight: bold;
}

